$font-stack: 'Poppins', sans-serif;
$color:#E90081;

*{
	box-sizing:border-box;
}

h2{
	font-family:$font-stack;
}

a{
	font-family:$font-stack;
	text-decoration:none;
}

p{
	line-height:1.4;
	font-family: 'Open Sans', sans-serif;
}


/* 1 NAV ================================= */

.navcontainer{
	display:flex;
    justify-content: space-between;
    background-color:$color;
    background-image: url(../img/pplogo.png);
    background-repeat:no-repeat;
    background-size: 50px;
    background-position:97%;
    padding:10px;


	ul{
	margin:20px 0 20px;
	margin-right:40px;

	}

	a{
		text-decoration:none;
		padding:20px;
		font-family:$font-stack;
		margin-top:30px;
		color:white;

		&:hover{
			color:#000;
			transition: 0.2s;
			
		}
	}


}


/* 2 Hero/HomePage ================================= */

.hero{

	height:800px; 
	background-image:url(../img/headerBC.png);
	background-repeat:no-repeat;
	background-size:cover; 
	background-position:20%;
	background-attachment:fixed;

	// img{
	// 	width: 100%;
	// 	height: 100%;
	// }

}

.petition{
	background-color:#fff;
	height:400px;
	margin:40px;
	display:flex;
	flex-wrap:wrap;


	h2{
		font-size: 5em;
		text-align:center;
		color:black;
		font-weight: bold;
		width: 100%;


	}

	p{
		font-size: 1.4em;
		color:$color;
		width: 40%;
		margin-right: 230px;
    	margin-left: 130px;

	}
}

button{
	width:25%;
	border:2px solid black;
	border-radius:inherit;
	color:black;
	height:75px;
	margin-top:20px;

	a{
		color:$color;
		font-size: 2em;
		text-align:center;
		padding-top:2px;
		text-decoration:none;
	}
}


.trump{
	background-image:url(../img/trumpapp.jpg);
	background-size:cover;
	background-position:20%;
	background-repeat: no-repeat;
	width:100%;
	background-color:$color;
	background-blend-mode:darken;
	padding: 134px 50px;

   


	h2{
    font-size: 2.3em;
    width: 55%;
    color:white;
    line-height: 45px;
    margin-bottom: 40px;
  	
	}


	
	a{
		background-color:rgba(0,0,0,0);
		height: 20px;
		width: 150px;
		padding:20px;
		// padding-left: 31px;
		border: 2px solid white;
		color:white;	
		// display: block;
		margin: 0 auto 0 500px;
		text-align: center; 

	}
	

}


.impact{
	position:relative;


	video{
		width: 100%;
		position: relative;

	}

	h2{
	  	font-size: 5em;
    	width: 80%;
	    color:white;
	    line-height: 1.2;
	    position:absolute; 
	    top:10%;
	    padding:30px;
	}
	
	p{
		color:white;
		position:absolute;
		top:56%;
		width: 50%;
		padding:40px;
		font-size: 1.5em;

	}

	a{
		position:absolute;
		background-color:rgba(0,0,0,0);
		height: 20px;
		width: 150px;
		padding: 20px 20px 36px 20px;
		border: 2px solid $color;
		color:$color;
		top:70%;
		left:80%;
		text-align: center;
	}
}


.atrisk{
	width:100%;
	padding:50px;

	h2{
		color:black;
		font-size: 2.3em;
		width: 45%;
		margin-bottom: 20px;


		}
	p{
		color:$color;
		font-size: 1em;
		width: 25%;

	}
}

.info-container{
	display:flex;
	width:100%;
	height:350px;
	justify-content:space-around;


}


.info{
	border: 2px solid $color;
	width:20%;
	align-self: flex-start;
	// max-height:85px;
	// overflow:hidden;

	h2{
		color:$color;
		font-size: 1.2em;
		padding:12px;
	
	}

	p{
		color:black;
		padding:0;
		max-height: 0;
		overflow: hidden;
	}

	&:hover{
		color:black;
	

		p {
			padding:20px;
			max-height: 300px;
		}
	}

}


